import logo from './logo.svg';
import './App.css';
import { Route, Routes, Navigate  } from 'react-router-dom';
import { useRecoilValue, useRecoilState } from 'recoil';
import userInfoState from './recoil/atoms/userInfoState';
import dmPushState from './recoil/atoms/dmPushState';

import userAjax from './ajax/userAjax';

import SignInPage from './pages/SignInPage';
import SignUpPage from './pages/SignUpPage';
import FeedPage from './pages/FeedPage';
import ProfilePage from './pages/ProfilePage';
import DmPage from './pages/DmPage';

import { useCallback, useEffect } from 'react';
import { getFirebaseToken, onMessageListener } from './firebaseConfig';

                   

function App() {  
  const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);      
  const [ dmPush, setDmPush ] = useRecoilState(dmPushState);

  const handleGetFirebaseToken = useCallback(() => {

    if(!userInfo.isLogin) {
      return;
    }

    getFirebaseToken().then(firebaseToken => {
      if (firebaseToken) {
        console.log(firebaseToken);
       
        if(userInfo.isLogin && userInfo.firebaseToken !== firebaseToken) {
          setUserInfo(prev => ( {...prev, firebaseToken }));

          //변경된 token을 서버에 보내준다.
          patchFirebaseToken(firebaseToken);
        }
      }
    });
  }, [userInfo.isLogin]);


  const patchFirebaseToken = async (firebaseToken) => {
    const params = {
      iuser: userInfo.iuser
      , firebaseToken
    }
    const { data } = await userAjax.patchUserFirebaseToken(params);    
  }

  useEffect(() => {

    if(Notification.permission !== 'granted') {
      Notification.requestPermission().then(permission => {    
        if (permission === 'granted') {
            handleGetFirebaseToken();
            console.log('사용자가 승인함!!!');
        }
      });
    } else {
      handleGetFirebaseToken();
      console.log('사용자가 승인함!!!');
    }

  }, [userInfo.isLogin]);

  onMessageListener()
  .then(payload => {    
    const { title, body } = payload.notification;
    switch(title) {
      case 'dm':
        const data = JSON.parse(body);

        setDmPush(prev => {
          const copyDm = JSON.parse(JSON.stringify(prev));
          const prevDm = copyDm.dmList[data.idm] ?  copyDm.dmList[data.idm] : { unCheckedMsgCnt: 0, data: null };
          prevDm.unCheckedMsgCnt = prevDm.unCheckedMsgCnt + 1;
          prevDm.data = data
  
          copyDm.dmList[data.idm] = prevDm;
          copyDm.unCheckedMsgTotalCnt = dmPush.unCheckedMsgTotalCnt + 1;
          return copyDm;
        });
        break;
    }
  })
  .catch(e => console.log(e));

  return (
    <Routes>
      {
        userInfo.isLogin 
        ? 
        (
          <>
            <Route path='/' element={<Navigate to="/feed" />} />
            <Route path='/feed' element={<FeedPage />} />
            <Route path='/profile/:iuser' element={<ProfilePage />} />
            <Route path='/dm/:otherPersonIuser?' element={<DmPage />} />
          </>
        )
        :
        (
          <>
            <Route path='/' element={<Navigate to="/signin" />} />
            <Route path='/signin' element={<SignInPage />} />
            <Route path='/signup' element={<SignUpPage />} />
          </>
        )
      }
      <Route path="*" element={<Navigate to="/" />} />
    </Routes>
  );
}

export default App;
