import customAxios from "./customAxios";

export default {
    procSignin: async (param) => {
        return await customAxios.post('/user/signin', param);
    },

    procSignup: async (param) => {
        return await customAxios.post('/user/signup', param);
    },

    getProfileInfo: async (params) => {
        return await customAxios.get('/user', { params });
    },

    patchUserFirebaseToken: async params => {
        return await customAxios.patch('/user/firebase-token', params);
    },
    
    patchUserPic: async params => {
        return await customAxios.patch('/user/pic', params);
    },



    postFollow: async params => {
        return await customAxios.post('/user/follow', params);
    }
    
}