import React, { useState } from 'react';
import ProfileImg from './ProfileImg';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';
import feedAjax from '../ajax/feedAjax';

const FeedCommentBox = ({ idx, props, onDelComment }) => {    
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    

    const delComment = async e => {
        
        const params = {
            ifeed_comment: props.ifeedComment
            , logined_iuser: userInfo.iuser
        }
        
        const { data } = await feedAjax.delFeedComment(params);
        if(!data.result) {
            alert('삭제 실패!');
            return;
        }
        onDelComment(idx);
    }

    return (
        <div className="cmtItemCont">
            <div className="cmtItemProfile">
                <ProfileImg clasValue="profile pointer" size="24" pic={props.writerPic} iuser={props.writerIuser} />
                {
                    userInfo.iuser === props.writerIuser &&
                    <i className="fa fa-trash pointer" onClick={delComment}></i>
                }
            </div>
            <div className="cmtItemCtnt">
                <div className="pointer">{props.writerNm}</div>
                <div>{props.comment}</div>
            </div>
        </div>
    );
}

export default FeedCommentBox;