import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import ProfileImg from '../components/ProfileImg';
import { Link, useNavigate } from 'react-router-dom';
import feedAjax from '../ajax/feedAjax';
import FeedCommentBox from './FeedCommentBox';

import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

import styles from './FeedBox.module.css';
import 'swiper/css';

const FeedBox = ({ props, iuser, feedIdx=0, onDelFeed, ynDel }) => {    
    const [ isMoreComment, setIsMoreComment ] = useState(props.isMoreComment);
    const [ commentList, setCommentList ] = useState(props.comments);
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    const [ isFav, setIsFav ] = useState(props.isFav);
    const [ inputs, setInputs ] = useState({ comment: '' });

    const { comment } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const onKeyPressComment = async e => {
        if(e.key === 'Enter') {
            await insComment();
        }
    }

    //const profilePicSrc = '/fimg/defaultProfileImg.png';

    const getDateTimeInfo = dt => {
        const nowDt = new Date();
        const targetDt = new Date(dt);
    
        const nowDtSec = parseInt(nowDt.getTime() / 1000);
        const targetDtSec = parseInt(targetDt.getTime() / 1000);
    
        const diffSec = nowDtSec - targetDtSec;
        if(diffSec < 120) {
            return '1분 전';
        } else if(diffSec < 3600) { //분 단위 (60 * 60)
            return `${parseInt(diffSec / 60)}분 전`;
        } else if(diffSec < 86400) { //시간 단위 (60 * 60 * 24)
            return `${parseInt(diffSec / 3600)}시간 전`;
        } else if(diffSec < 2592000) { //일 단위 (60 * 60 * 24 * 30)
            return `${parseInt(diffSec / 86400)}일 전`;
        }
        return targetDt.toLocaleString();
    }

    const styleItem = `${styles.Item} mb-3 p-3 w400`;
    
    const toggleFav = async () => {
        const params = {
            iuser
            , ifeed: props.ifeed
        }
        const { data } = await feedAjax.toggleFeedFav(params);
        if(data) {
            setIsFav(data.result);         
        }
    }

    const insComment = async e => {
        if(comment.length === 0) {
            alert('댓글을 작성해 주세요.');
            return;
        }

        const params = {
            ifeed: props.ifeed
            , iuser: userInfo.iuser
            , comment
        }        

        const { data } = await feedAjax.postFeedComment(params);
        if(data.result) {
            setInputs({
                ...inputs
                , ['comment']: ''
            })

            const commentItem = {
                ifeedComment: data.result
                , writerIuser: userInfo.iuser
                , writerNm: userInfo.nm
                , writerPic: userInfo.pic
                , comment
            }
            
            setCommentList(prev => {
                return [ ...prev, commentItem ];
            });
        }   

    }

    const getMoreComment = async e => {
        const params = {
            ifeed: props.ifeed
        }

        const { data } = await feedAjax.getFeedComment(params);
        if(data && data.length > 0) {
            setCommentList([
                ...commentList,
                ...data
            ]);
        }
        if(isMoreComment) {
            setIsMoreComment(0);
        }
    }

    function delComment(idx) {
        setCommentList(prev => {            
            prev.splice(idx, 1);
            return [ ...prev ];
        });
    }

    const delFeed = async e => {
        if(!window.confirm('삭제하시겠습니까?')) { return; }
        const params = {
            ifeed: props.ifeed
            , iuser: userInfo.iuser
        }
        const { data } = await feedAjax.delFeed(params);
        if(!data.result) { console.log('삭제를 실패하였습니다.'); return; }
        onDelFeed(feedIdx);
    }

    return (
        <div className={styleItem}>
            <div className='d-flex flex-row ps-3 pe-3'>
                <div className="d-flex flex-column justify-content-center">
                    <Link to={`/profile/${props.writerIuser}`}><ProfileImg iuser={props.writerIuser} pic={props.writerPic} size={30} clsValue='pointer profile' /></Link>
                </div>
                <div className="p-3 flex-grow-1">
                    <div><Link to={`/profile/${props.writerIuser}`}><span className="pointer">{props.writerNm}</span></Link> - { getDateTimeInfo(props.createdAt) }</div>
                    <div>{props.location == null ? '' : props.location}</div>
                </div>
                {
                    ynDel && props.writerIuser && userInfo.iuser &&
                    <div className="d-flex flex-column justify-content-center">
                        <i className="fa fa-trash pointer" onClick={delFeed}></i>
                    </div>
                }
            </div>
            
            <Swiper
                modules={[Navigation, Pagination, Scrollbar, A11y]}
                spaceBetween={50}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                scrollbar={{ draggable: true }}>

                {
                    props.pics 
                    ? 
                    props.pics.map((item, idx) => {
                                return (
                                    <SwiperSlide key={idx}>
                                        <img src={item} className="w614" />
                                    </SwiperSlide>
                                );
                            })
                    : null
                }

            </Swiper>            

            <div className="favCont p-2 d-flex flex-row">
                <i className={`${isFav ? 'fas':'far'} fa-heart pointer rem1_2 me-3`} onClick={toggleFav}></i>
                {
                    props.writerIuser !== userInfo.iuser && 
                    <div>
                        <Link to={`/dm/${props.writerIuser}`}><svg aria-label="Direct Message" className="_8-yf5 " color="#262626" fill="#262626" height="24" role="img" viewBox="0 0 24 24" width="24"><line fill="none" stroke="currentColor" strokeLinejoin="round" strokeWidth="2" x1="22" x2="9.218" y1="3" y2="10.083"></line><polygon fill="none" points="11.698 20.334 22 3.001 2 3.001 9.218 10.084 11.698 20.334" stroke="currentColor" strokeLinejoin="round" strokeWidth="2"></polygon></svg></Link>
                    </div>
                }
            </div>
            {
                props.contents 
                ? <div className="itemCtnt p-2" dangerouslySetInnerHTML={{__html: props.contents.replaceAll('\n', '<br>')}}></div>
                : null
            }
            <div>
                <div>
                    {
                        commentList &&                        
                        commentList.map((item, idx) => <FeedCommentBox key={item.ifeedComment} idx={idx} props={item} onDelComment={delComment} />)                        
                    }
                    {
                        isMoreComment
                        ? 
                        <div>
                            <span className="pointer" onClick={getMoreComment}>댓글 더보기</span>
                        </div>                        
                        : null
                    }
                </div>
                <div className="p-2 d-flex flex-row div-top">
                    <input type="text" name="comment" className="flex-grow-1 my_input back_color" placeholder="댓글을 입력하세요..." value={comment} onChange={onChangeInput} onKeyPress={onKeyPressComment} />
                    <button className="btn btn-outline-primary" onClick={insComment}>등록</button>
                </div>
            </div>
        </div>
    );
}

export default FeedBox;