import { useState, useEffect, useCallback } from 'react';
import customAxios from "./customAxios";

const useFetch = (url, page, params) => {
    const [ hasMore, setHasMore ] = useState(true);
    const [ isLoading, setIsLoading ] = useState(false);
    const [ error, setError ] = useState(false);    
    const [ msgList, setMsgList ] = useState({ isScroll: true, list: [] })

    const sendQuery = useCallback(async () => {
        if(params && params.idm && params.idm > 0) {
            if(page === 1) {
                setMsgList({ isScroll: true, list: [] })
                //setList([]);
            }
            try {
                if(params.page != page) {
                    params.page = page;
                }
                console.log(params);
                setIsLoading(true);
                setError(false);
                const { data } = await customAxios.get(url, { params });
                if(!data) {
                    throw new Error('서버에 오류가 있습니다.');
                }
                if(data.length > 0) {
                    //setList(prev => [...new Set([ ...data.reverse(), ...prev ])]);
                    setMsgList(prev => {
                        const copyPrev = { ...prev };
                        copyPrev.list = [...new Set([ ...data.reverse(), ...copyPrev.list ])]
                        copyPrev.isScroll = page === 1;
                        return copyPrev;
                    });
                }
                setHasMore(data.length > 0);
                setIsLoading(false);
            } catch(e) {
                setError(e);            
            } finally {            
            }
        } else {
            setHasMore(false);
        }
    }, [page, params.idm]);

    useEffect(() => {
        sendQuery();
    }, [page, sendQuery])

    //return { isLoading, error, list, setList, hasMore }
    return { isLoading, error, msgList, setMsgList, hasMore }
}

export default useFetch;