import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import userAjax from '../ajax/userAjax';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const SignInPage = () => {
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    const navigate = useNavigate();
    const [ inputs, setInputs ] = useState({uid: '', upw: ''});
    
    const { uid, upw } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const onClickSignin = async () => {
        console.log(inputs);
        const { data } = await userAjax.procSignin(inputs);
        if(!data.result) {
            alert('에러 발생!');
            return;
        }

        console.log('login!!');
        console.log(data);

        const { result, iuser, nm, pic, firebaseToken } = data;

        switch(result) {            
            case 1:
                setUserInfo({ isLogin: true, iuser, nm, pic, firebaseToken });
                navigate('/feed');
                return;
            case 2:
                alert('아이디를 확인해 주세요.');
                return;
            case 3:
                alert('비밀번호를 확인해 주세요.');
                return;
        }
    }

    return (
        <section  className="back_color">
            <div  className="container">
                <h1>Sign in</h1>
                <div>
                    <div><label><input type="text" name="uid" placeholder="id" value={uid} onChange={onChangeInput} /></label></div>
                    <div><label><input type="password" name="upw" placeholder="password" value={upw} onChange={onChangeInput} /></label></div>
                    <div><input type="button" id="btn_submit" value="Sign in" onClick={onClickSignin} /></div>
                    <div>
                        <Link to={'/signup'}>Sign up</Link>                            
                    </div>
                </div>
            </div>
        </section>
    );
}

export default SignInPage;