import React, { useState, useRef, useEffect, useCallback } from 'react';
import Header from '../components/Header';
import useFetch from '../ajax/useFetch';
import useDmMsgFetch from '../ajax/useDmMsgFetch';
import dmAjax from '../ajax/dmAjax';
import myUtils from '../utils/myUtils';
import { useParams } from "react-router-dom";
import ProfileImg from '../components/ProfileImg';

import { useRecoilValue, useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';
import dmPushState from '../recoil/atoms/dmPushState';
import styles from './DmPage.module.css';

const DmPage = () => {    
    const { otherPersonIuser } = useParams();
    const [ paramIuser, setParamIuser ] = useState(otherPersonIuser ? parseInt(otherPersonIuser) : 0);
    
    const [ inputs, setInputs ] = useState({ msg: '' });
    const { msg } = inputs;
    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const userInfo = useRecoilValue(userInfoState);
    const [ dmPush, setDmPush ] = useRecoilState(dmPushState);

    const [ dmRoomPage, setDmRoomPage ] = useState(1);
    const { isLoading: isDmRoomLoading, error: dmRoomError, list: dmRoomList, setList: setDmRoomList, hasMore: dmRoomHasMore } = useFetch('/dm', dmRoomPage, { loginedIuser: userInfo.iuser });
    
    const defaultProfilePic = '/fimg/defaultProfileImg.png'
    
    const profilePicError = e => {
        e.currentTarget.src = defaultProfilePic;
    }        
    
    const dmRoomObserverRef = useRef();
    const dmRoomObserver = node => {
        if(isDmRoomLoading) return;
        if(dmRoomObserverRef.current) {
            dmRoomObserverRef.current.disconnect();
        }        
        dmRoomObserverRef.current = new IntersectionObserver(([entry]) => {
            if(entry.isIntersecting && dmRoomHasMore) {
                setDmRoomPage(prev => prev + 1);
            }
        }, {
            root: document.querySelector('.dm_user_list_container'),
            rootMargin: "200px",
            threshold: 0
          });

        node && dmRoomObserverRef.current.observe(node);
    };

    const onClickDmRoom = useCallback(idm => {
        console.log(idm);
        setIsScrollDown(1);
        setMsgPage(1);
        setIdm(idm);

        setDmPush(prev => {
            const copyDm = JSON.parse(JSON.stringify(prev));
            const prevDm = copyDm.dmList[idm] ? copyDm.dmList[idm] : { unCheckedMsgCnt: 0, msg: null };
            copyDm.unCheckedMsgTotalCnt -= prevDm.unCheckedMsgCnt;            
            if(copyDm.unCheckedMsgTotalCnt < 0) {
                copyDm.unCheckedMsgTotalCnt = 0;
            }

            prevDm.unCheckedMsgCnt = 0;
            copyDm.dmList[idm] = prevDm;
            return copyDm;
        });
    }, []);
    
    const [ idm, setIdm ] = useState(0);
    const [ msgPage, setMsgPage ] = useState(1);
    const { isLoading: isMsgLoading, error: msgError, msgList, setMsgList, hasMore: msgHasMore} = useDmMsgFetch('/dm/msg', msgPage, { idm });
    //const [ viewMsgList, setViewMsgList ] = useState(msgList);

    const [ isScrollDown, setIsScrollDown ] = useState(0);

    useEffect(()=> {
        console.log(`idm: ${idm}`);
        console.log(dmPush);
        if(dmPush.dmList[idm] && dmPush.dmList[idm].unCheckedMsgCnt > 0) {            
            console.log('글 작성 됨');
            setDmPush(prev => {
                const copyDm = JSON.parse(JSON.stringify(prev));
                
                const prevDm = copyDm.dmList[idm];                
                const data = prevDm.data;
                setMsgList(prev => {
                    const copyPrev = { ...prev };
                    copyPrev.list = [...new Set([ ...copyPrev.list, data ])]
                    copyPrev.isScroll = true;
                    return copyPrev;
                });
                
                copyDm.unCheckedMsgTotalCnt -= prevDm.unCheckedMsgCnt;                
                if(copyDm.unCheckedMsgTotalCnt < 0) {
                    copyDm.unCheckedMsgTotalCnt = 0;
                }

                prevDm.unCheckedMsgCnt = 0;                
                copyDm.dmList[idm] = prevDm;
                return copyDm;
              });
        } 

        dmRoomList.forEach((item, idx) => {
            const pushDm = dmPush.dmList[item.idm];
            if(pushDm && pushDm.data && pushDm.data.msg && pushDm.data.msg !== item.lastMsg) {                
                const originDmRoomList = [ ...dmRoomList ];                    
                originDmRoomList[idx].lastMsg = pushDm.data.msg;
                setDmRoomList(originDmRoomList);
            }
        });
        
    }, [dmPush]);

    //otherPersonIuser 파라미터가 넘어오면 메시지 리스트 나타나도록 처리
    useEffect(() => {
        if(paramIuser) {           
            if(dmRoomList.length === 0) {
                makeDmRoom();
            } else {
                console.log('dmRoomList useEffect!!!');
                const selectedDmRoom = dmRoomList.filter(item => item.otherPersonIuser === paramIuser);
    
                if(selectedDmRoom.length === 1) {
                    setIdm(selectedDmRoom[0].idm);
                    setParamIuser(0);                
                } else {
                    makeDmRoom();
                }                   
            }
        } 
    }, [dmRoomList])


    const makeDmRoom = useCallback(async () => {
        //방이 없으니 만든다.
        const params = {
            loginedIuser: userInfo.iuser
            , otherPersonIuser: paramIuser
        }
        const { data } = await dmAjax.postDm(params);
        if(data && data.idm) {
            setDmRoomList(prev => [...prev, data]);        
        }
    }, [paramIuser]);

    /*
    useEffect(()=> {
        setViewMsgList(prev => [])
    }, msgList)
    */
    const msgListRef = useRef();
    const scrollToBottom = () => {
        if (msgListRef.current) {
            msgListRef.current.scrollTop = msgListRef.current.scrollHeight;
        }
    };
    useEffect(() => { 
        console.log(msgList);
        if(msgList.isScroll) {
            scrollToBottom();
        }
    }, [msgList.list]);


    const msgObserverRef = useRef();
    const msgRoomObserver = node => {
        if(isMsgLoading) return;
        if(msgObserverRef.current) {
            msgObserverRef.current.disconnect();
        }        
        msgObserverRef.current = new IntersectionObserver(([entry]) => {
            if(entry.isIntersecting && msgHasMore) {
                setMsgPage(prev => prev + 1);
            }
        }, {
            root: document.querySelector('.dm_msg_container'),
            rootMargin: "200px",
            threshold: 0
          });

        node && msgObserverRef.current.observe(node);
    };

    const onClickMsgSend = async e => {
        //setIsScrollDown(1);
        if(msg.length === 0) { return; }
        const params = {
            idm
            , loginedIuser: userInfo.iuser
            , loginedPic: userInfo.pic
            , msg
        }

        const { data } = await dmAjax.postDmMsg(params);
        if(data && data.result) {
            const msgParams = {
                ...params
                , writerIuser: userInfo.iuser
                , seq: data.result
                , createdAt: myUtils.getNormalDatetime(new Date())
            }
            //setMsgList(prev => [...prev, msgParams]);
            setMsgList(prev => {
                const copyPrev = { ...prev }
                copyPrev.list = [ ...prev.list, msgParams ];
                copyPrev.isScroll = 1;
                return copyPrev;
            })
            setInputs({
                ...inputs
                , ['msg']: ''
            });            
            
            dmRoomList.forEach((item, idx) => {
                if(item.idm === idm) {
                    const originDmRoomList = [ ...dmRoomList ];                    
                    originDmRoomList[idx].lastMsg = msg;
                    setDmRoomList(originDmRoomList);
                }
            });           
        }
    }

    const onKeyPressMsg = async e => {
        if(e.key === 'Enter') {
            await onClickMsgSend();
        }
    }

    const onClickDelMsg = async (seq) => {
        console.log(seq);        
        const params = {
            idm, seq
            , iuser: userInfo.iuser
        }

        const { data } = await dmAjax.delDmMsg(params);

        if(data && data.result) {            
            setMsgList(prev => {
                const copyPrev = { ...prev }                
                const copyMsgList = msgList.list.filter(item => item.seq !== seq);
                const beforLastMsg = msgList.list[msgList.list.length - 1];
                if(beforLastMsg.seq === seq) { //마지막 메시지 삭제라면
                    const nowLastMsg = copyMsgList[copyMsgList.length - 1];
                    dmRoomList.forEach((item, idx) => {
                        if(item.idm === idm) {
                            const originDmRoomList = [ ...dmRoomList ];                    
                            originDmRoomList[idx].lastMsg = nowLastMsg.msg;
                            setDmRoomList(originDmRoomList);
                        }
                    });
                }
                copyPrev.list = copyMsgList;
                copyPrev.isScroll = false;
                return copyPrev;
            })
        }
    }

    return (
        <div>
            <Header />
            <section className="d-flex flex-row w-full h-full justify-content-center">           
                <div className="d-flex flex-row p-3 w800">
                    <div className="dm_user_list_container w300 over_y_a h600">                    
                        { (msgError || dmRoomError) && <div>Error!</div> }                     
                        { isDmRoomLoading && <div>Loading...</div> }
                        {
                            dmRoomList.map(item => {                                    
                                    return(
                                        <div key={item.idm} className={`${styles.DmRoomItem} pointer d-flex flex-row w-full ${item.idm === idm ? 'selected' : ''}`} onClick={() => {onClickDmRoom(item.idm)}} style={{'marginBottom': '10px', 'marginTop': '10px', 'padding': '5px'}}>
                                            <div className="w50 d-flex justify-content-center align-items-center">                                                
                                                <img src={item.otherPersonPic} className="profile w30 h30" onError={profilePicError} />
                                            </div>
                                            <div className="flex-grow-1">
                                                <div>{item.otherPersonNm}</div>
                                                <div>{item.lastMsg == null ? '' : item.lastMsg}</div>
                                            </div>
                                            <div className="w50 d-flex justify-content-center align-items-center">
                                                <span className="position-relative">
                                                    <span className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${(!dmPush.dmList[item.idm] || dmPush.dmList[item.idm].unCheckedMsgCnt === 0) ? 'd-none': ''}`}>
                                                        <span>{(dmPush.dmList[item.idm] && dmPush.dmList[item.idm].unCheckedMsgCnt) ? dmPush.dmList[item.idm].unCheckedMsgCnt : ''}</span>
                                                        <span className="visually-hidden">unread messages</span>
                                                    </span>
                                                </span>
                                            </div>                                            
                                        </div>
                                    )
                                }
                            )
                        }                   
                        { dmRoomHasMore && <div ref={dmRoomObserver}/> }
                    </div>
                    
                    <div className="flex-grow-1 d-flex flex-column">
                        <div className="dm_msg_container over_y_a d-flex flex-column flex-grow-1 w-full h600" ref={msgListRef}>                            
                            { msgHasMore && <div ref={msgRoomObserver}/> }
                            { isMsgLoading && <div>Loading...</div>}
                            {
                                msgList.list.map(item => 
                                    item.writerIuser === userInfo.iuser
                                    ? // 내가 보낸 메시지
                                    <div key={item.seq} className="col-6 dropdown" style={{alignSelf: 'flex-end'}}>
                                        <div className="alert alert-warning">
                                            <a href="#" role="button" id={`dropdownMenuLink_${item.seq}`} data-bs-toggle="dropdown" aria-expanded="false" className="">
                                                <b>{item.msg}</b>
                                            </a>
                                            <ul className="dropdown-menu" aria-labelledby={`dropdownMenuLink_${item.seq}`}>
                                                <li onClick={() => onClickDelMsg(item.seq)}>
                                                    <span className="dropdown-item" style={{'color': 'red'}}>전송 취소 <i className="fa fa-trash pointer"/></span>
                                                </li>
                                            </ul>
                                        </div>                                        
                                        <div>{item.createdAt}</div>
                                    </div>
                                    : // 상대방이 보낸 메시지
                                    <div key={item.seq} className="col-6 d-flex">
                                        <ProfileImg iuser={item.writerIuser} pic={item.writerPic} size={50} clsValue='w50 h50 profile' />                                       
                                       <div className="flex-grow-1">                                            
                                            <div className="alert"><b>{item.msg}</b></div>
                                            <div>{item.createdAt}</div>
                                       </div>
                                    </div>
                                )
                            }
                        </div>
                        <div className="dm_form h100 w-full">
                            <div className="input-group mb-3">
                                <input type="text" name="msg" className="form-control" aria-label="Recipient's username" aria-describedby="button-addon2" value={msg} onChange={onChangeInput} onKeyPress={onKeyPressMsg} disabled={idm === 0} />
                                <div className="input-group-append">
                                    <button className="btn btn-outline-secondary" type="button" id="button-send" onClick={onClickMsgSend} disabled={idm === 0}>전송</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
        
    );
}

export default DmPage;