import React, { useState, useEffect, useCallback } from 'react';
import Header from '../components/Header';
import FeedBox from '../components/FeedBox';
import feedAjax from '../ajax/feedAjax';


import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const FeedPage = () => {
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    
    //const [ page, setPage ] = useState(1); 
    let page = 1;
    const [ listData, setListData ] = useState([]);
    let isLoading = false;
    
    const [ error, setError ] = useState(null);
 

    const handleScroll = () => {       
        //if (parseInt(window.innerHeight + document.documentElement.scrollTop) !== document.documentElement.offsetHeight || isLoading) {
        
        console.log(isLoading);
        if (isLoading || (document.documentElement.offsetHeight - parseInt(window.innerHeight + document.documentElement.scrollTop)) > 500) {
          return;
        }
        getData();
    };



    const getData = useCallback(
        async function() {
            isLoading = true;
            setError(null);

            console.log('getData');
            try {
                console.log(page);
                const res = await feedAjax.getFeed({page, loginedIuser: userInfo.iuser });
                if(res.data) {            
                    setListData(prevData => [...prevData, ...res.data]);
                }
            //    setPage(page + 1);                
                console.log(`page::: ${page}`)
                page++;
            } catch(e) {
                setError(error);
            } finally {
                //setIsLoading(false);
                isLoading = false
            }
        }, [page, setError]
    );

    useEffect(() => {
        getData();
    }, []);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isLoading]);

    
    return (
        <div>
            <Header setListData={setListData}></Header>
            <section className="back_color">
                <div className="container d-flex flex-column align-items-center">
                    { listData.map(item => <FeedBox key={item.ifeed} props={item} iuser={userInfo.iuser} /> )}
                    { isLoading && <p>Loading...</p> }
                    { error && <p>Error: {error.message}</p> }
                </div>
            </section>
        </div>
    );
}

export default FeedPage;