import customAxios from "./customAxios";

export default {
    getFeed: async (params) => {
        console.log(params)
        return await customAxios.get('/feed', { params });
    },    

    postFeed: async (params) => {
        return await customAxios.post('/feed', params);
    },

    delFeed: async params => {
        return await customAxios.delete('/feed', { params });
    },




    toggleFeedFav: async(params) => {
        return await customAxios.get('/feed/fav', { params });
    },



    getFeedComment: async params => {
        return await customAxios.get('/feed/comment', { params });
    },

    postFeedComment: async params => {
        return await customAxios.post('/feed/comment', params);
    },
    delFeedComment: async params => {
        return await customAxios.delete('/feed/comment', { params });
    }

}