import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import userAjax from '../ajax/userAjax';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const SignUpPage = () => {
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);  
    const navigate = useNavigate();
    const [ inputs, setInputs ] = useState({uid: '', upw: '', chkUpw: '', nm: '', pic: ''});
    
    const { uid, upw, chkUpw, nm, pic } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        console.log(name);
        setInputs({
            ...inputs
            , [name]: value
        });
    }
    
    const onClickSignup = async () => {     
        const { upw, chkUpw, uid, nm, pic } = inputs;

        if(upw !== chkUpw) {
            alert('비밀번호를 확인해 주세요.');
            return;
        }

        const param = { uid, upw, nm, pic };
        
        const { data } = await userAjax.procSignup(param);
        console.log(data);
        if(data.result === 0) {
            alert('에러 발생!');
            return;
        }        

        setUserInfo({ isLogin: true, iuser: data.result, nm, pic });
        navigate('/feed');
    }


    return (
        <section className="back_color">
            <div className="container">
                <h1>Sign up</h1>
                <div>
                    <div><label><input type="text" name="uid" placeholder="id" value={uid} onChange={onChangeInput} /></label></div>
                    <div><label><input type="password" name="upw" placeholder="password" value={upw} onChange={onChangeInput} /></label></div>
                    <div><label><input type="password" name="chkUpw" placeholder="confirm password" value={chkUpw} onChange={onChangeInput} /></label></div>
                    <div><label><input type="text" name="nm" placeholder="name" value={nm} onChange={onChangeInput} /></label></div>
                    <div><label><input type="text" name="pic" placeholder="profile pic" value={pic} onChange={onChangeInput} /></label></div>
                    <div><input type="button" value="Sign up" onClick={onClickSignup} /></div>
                    <div>
                        <Link to={'/signin'}>Sign ip</Link>                            
                    </div>
                </div>
            </div>
        </section>
    );
}

export default SignUpPage;