import './ProfilePage.css';
import styled from 'styled-components';
import React, { useState, useEffect, useCallback } from 'react';
import { useParams } from "react-router-dom";
import Header from '../components/Header';
import FeedBox from '../components/FeedBox';
import userAjax from '../ajax/userAjax';
import feedAjax from '../ajax/feedAjax';

import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

import ProfileImg from '../components/ProfileImg';


const ProfilePage = () => {
    let localActiveTabIdx = 0;
    //let page = 1;
    let isFinish = false;

    let favPage = 1;
    let isFavFinish = false;

    //let isLoading = false;

    const rowCount = 20;
   
    const [ isLoading, setIsLoading ] = useState(false);
    const [ page, setPage ] = useState(1);
    const [ activeTabIdx, setActiveTabIdx ] = useState(0);
    const [ inputs, setInputs ] = useState({ inputPic: '' });
    const { inputPic } = inputs;
    const [ followBtnTxt, setFollowBtnTxt ] = useState('팔로우');

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    
    const { iuser } = useParams();    
    const [ targetIuser, setTargetIuser ] = useState(iuser);    
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);        
    const [ profileInfo, setProfileInfo ] = useState({ favCnt: 0, feedCnt: 0, following: 0, follower: 0, followState: 0, nm: '', createdAt: '', pic: '' });    
    const [ followState, setFollowState ] = useState(0);

    const [ myFeedListData, setMyFeedListData ] = useState([]);
    const [ favFeedListData, setFavFeedListData ] = useState([]);
    const [ error, setError ] = useState(null);


    const handleScroll = () => {    
        
        console.log(isLoading);

        if(isLoading || document.documentElement.offsetHeight - parseInt(window.innerHeight + document.documentElement.scrollTop) > 500) {
            return;
        } else if(localActiveTabIdx === 0) {
            if(isFinish) {
                return;
            }
            getMyFeedList();
        } else if(localActiveTabIdx === 1) {
            console.log(`isFavFinish : ${isFavFinish}`)
            if(isFavFinish) {
                return;
            }
            console.log('getMyFavFeedList')
            getMyFavFeedList();            
        }        
    };

    /*
    const handleScroll = useCallback(() => {    
        console.log(`activeTabIdx: ${localActiveTabIdx}`); 
        if(isLoading || document.documentElement.offsetHeight - parseInt(window.innerHeight + document.documentElement.scrollTop) > 500) {
            return;
        } else if(activeTabIdx === 0) {
            if(isFinish) {
                return;
            }
            getMyFeedList();
        } else if(activeTabIdx === 1) {
            if(isFavFinish) {
                return;
            }
            getMyFavFeedList();            
        }        
    }, [activeTabIdx]);
   */
    const getProfileInfoData = useCallback(
        async () => {
            console.log('프로필 데이터 가져오기')
            const params = {
                targetIuser: iuser
                , loginedIuser: userInfo.iuser
            }
            const { data } = await userAjax.getProfileInfo(params);        
            if(data) {
                setProfileInfo(data);
                setFollowState(data.followState);
            }
        }, [setProfileInfo]
    );

    const getMyFavFeedList = 
        async () => {            
            //isLoading = true;
            setIsLoading(true);
            setError(null);
    
            const params = {
                page: favPage
                , isFavList: 1
                , loginedIuser: userInfo.iuser
            }
    
            console.log('getMyFavFeedList');
            try {
                const { data } = await feedAjax.getFeed(params);            
                if(data) {            
                    setFavFeedListData(prevData => [...prevData, ...data]);
                }         
                console.log(data);   
                if(data.legnth < rowCount) { 
                    isFavFinish = true; 
                    return; 
                }
                console.log(`favPage: ${favPage}`);
                favPage++;
            } catch(e) {
                setError(error);
            } finally {
                //isLoading = false;
                setIsLoading(false);
            }
        };
    
    const getMyFeedList = 
        async () => {
            setIsLoading(true);
            //isLoading = true;
            setError(null);
    
            const params = {
                page
                , loginedIuser: userInfo.iuser
                , targetIuser: iuser
            }
    
            console.log('getMyFeedList');
            try {
                const { data } = await feedAjax.getFeed(params);            
    
                
                if(data) {            
                    setMyFeedListData(prevData => [...prevData, ...data]);
                }            
                if(data.legnth < rowCount) { 
                    isFinish = true; 
                    return;
                }
                //page++;
                setPage(page + 1);
            } catch(e) {
                setError(error);
            } finally {
                //isLoading = false;
                setIsLoading(false);
            }
        }
  

    const onChangeTabIdx = (idx) => {        
        setActiveTabIdx(idx);
        localActiveTabIdx = idx;                                        
    };

    const patchUserPicSend = async e => {
        const params = {
            iuser: userInfo.iuser
            , pic: inputPic
        }

        const { data } = await userAjax.patchUserPic(params);        
        if(!data.result) { return; }
        setUserInfo(prev => {
            const data = { ...prev }
            data.pic = inputPic;
            return data;
        });

        const $closeBtn = document.querySelector('#patchUserPicModal .btn-close');
        $closeBtn.click();

        setInputs({
            ...inputs
            , ['inputPic']: ''
        });
        
    }

    function onDelFeed(feedIdx) {
        console.log(feedIdx);
        setMyFeedListData(prev => {
            prev.splice(feedIdx, 1);
            return [ ...prev ];
        });
    }

    useEffect(() => {
        console.log('useEffect!!!')
        setMyFeedListData([]);
        setFavFeedListData([]);

        setPage(1);        
        favPage = 1;
        isFinish = false;
        isFavFinish = false;

        getProfileInfoData();        
        getMyFeedList();
        getMyFavFeedList();
    }, [iuser]);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [handleScroll, isLoading, page]);


    const getFollowStateText = state => {
        console.log(`state : ${state}`);
        switch(state) {
            case 0:
                return '팔로우';
            case 1:
            case 3:
                return '팔로우 취소';
            case 2:
                return '맞팔로우';

        }
    }

    const procFollow = async e => {
        const params = {
            fromIuser: userInfo.iuser
            , toIuser: iuser
        }
        //1-팔로우, 2-팔로워(맞팔로우), 3-팔로잉
        const { data } = await userAjax.postFollow(params);
        if(data) {            
            switch(data.result) {
                case 0:
                    setFollowState(followState - 1);
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower - 1
                    });
                    break;
                case 1:
                    setFollowState(followState + 1);                           
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower + 1                    });
                    break;                    
            }
        }
    }

    const tabs = [
        { name: '나의 Feed', content: myFeedListData },
        { name: '좋아요 Feed', content: favFeedListData },
      ];

    // styled-component로 기본 CSS 적용
    const TabContainer = styled.ul`
    display: flex;
    `;
    const TabButton = styled.li`
    list-style: none;
    border: solid 1px #bdc3c7;
    padding: 10px;
    font-weight: bold;
    cursor: pointer;
    ${props => props.selected && `color: #e74c3c;` }
    `;
    return (
        <section>
            <div className="container">
                <Header></Header>
                <div id="flexContainer">
                    <div>
                    <table>
                        <thead></thead>
                        <tbody>
                            <tr>
                                <td>게시물</td>
                                <td>{profileInfo.feedCnt}</td>
                                <td className="pl_10">좋아요</td>
                                <td>{profileInfo.favCnt}</td>
                                <td className="pointer follower pl_10">팔로워</td>
                                <td className="pointer follower">{profileInfo.follower}</td>
                                <td className="pointer follow pl_10">팔로우</td>
                                <td className="pointer follow">{profileInfo.following}</td>
                                {
                                    userInfo.iuser !== parseInt(iuser) &&
                                    <td className="pl_10">
                                        <input type="button" className="instaBtn" value={getFollowStateText(followState)} onClick={procFollow} />      
                                    </td>
                                }
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    <div>                           
                        {
                            parseInt(iuser) === userInfo.iuser 
                            ?
                            <a href="#" id="patchUserPicBtn" data-bs-toggle="modal" data-bs-target="#patchUserPicModal">               
                                <ProfileImg iuser={targetIuser} pic={userInfo.pic} size={300} clsValue='profile' />
                            </a>
                            :
                            <ProfileImg iuser={targetIuser} pic={profileInfo.pic} size={300} clsValue='profile' />
                        }                        
                    </div>
                    <table>
                        <thead></thead>
                        <tbody>                           
                            <tr>
                                <th>name</th>
                                <td>{profileInfo.nm}</td>
                            </tr>
                            <tr>
                                <th>reg date</th>
                                <td>{profileInfo.createdAt}</td>
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    </div>
                </div>

                <div id="item_container">
                    <TabContainer>
                        {
                            tabs.map((el, idx) => (
                                <TabButton
                                    key={el.index}
                                    onClick={
                                        () => {
                                            //console.log(idx);
                                            onChangeTabIdx(idx);
                                        }
                                    }
                                    selected={activeTabIdx === idx}
                                >
                                    {el.name}
                                </TabButton>
                            ))
                        }
                    </TabContainer>
                    <section>
                        { tabs[activeTabIdx].content.map((item, idx) => <FeedBox key={item.ifeed} props={item} iuser={userInfo.iuser} ynDel="1" feedIdx={idx} onDelFeed={onDelFeed} />) }

                    </section>
                </div>
                { isLoading && <div className="loading display-none"><img src="/fimg/loading.gif" /></div> }
                { error && <p>Error: {error.message}</p> }
            </div>


            <div className="modal fade" id="patchUserPicModal" tabIndex="-1" aria-labelledby="newFeedModalLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-xl">
                    <div className="modal-content" id="newFeedModalContent">
                        <div className="modal-header">
                            <h5 className="modal-title" id="newFeedModalLabel">프로필 사진 변경</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body" id="id-modal-body">                            
                            <div>pic: <input type="text" name="inputPic" onChange={onChangeInput} placeholder="사진" value={inputPic} /></div>                            
                            <div><button onClick={patchUserPicSend}>전송</button></div>
                        </div>
                    </div>
                </div>                
            </div>
        </section>
    );
}

export default ProfilePage;