import customAxios from "./customAxios";

export default {
    postDm: async params => {
        return await customAxios.post('/dm', params);
    },

    postDmMsg: async params => {
        return await customAxios.post('/dm/msg', params);
    },

    delDmMsg: async params => {
        return await customAxios.delete('/dm/msg', { params });
    }
}